.report-page-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f4f4f4;
}

.report-form {
  background: #fff;
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 50%;
  max-width: 800px;
}

.report-form h2 {
  text-align: center;
  margin-bottom: 20px;
}

.report-form label {
  display: block;
  margin-top: 20px;
}

.report-form select,
.report-form input {
  width: 100%;
  padding: 12px;
  margin-top: 10px;
  border-radius: 4px;
  border: 1px solid #ddd;
}

.report-form button {
  width: 100%;
  padding: 15px;
  margin-top: 30px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.report-form button:hover {
  background-color: #0056b3;
}

.error {
  color: red;
  margin-top: 20px;
  text-align: center;
}
