body.otp-page-body {
    margin: 0;
    font-family: Arial, sans-serif;
    background-color: #ffffff;
}

body.otp-page-body .otp-page {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #ffffff;
    height: 100vh;
}

body.otp-page-body .header-container {
    width: 100%;
    background-color: #282c34;
    color: white;
    padding: 20px 0;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    height: 70px;
}

body.otp-page-body .logo-container {
    position: absolute;
    left: 20px;
}

body.otp-page-body .logo {
    height: 80px;
    margin-top: 30px;
    margin-bottom: 30px;
}

body.otp-page-body .header-container h1 {
    margin: 0;
    font-size: 28px;
    align-self: center;
}

body.otp-page-body .otp-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    flex: 1;
    padding: 20px;
    width: 100%;
    max-width: 1200px;
}

body.otp-page-body .otp-image {
    width: 150px;
    height: 150px;
    margin-bottom: 20px;
}

body.otp-page-body .otp-inputs {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

body.otp-page-body .otp-inputs input {
    width: 50px;
    height: 50px;
    text-align: center;
    font-size: 24px;
    margin: 0 5px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

body.otp-page-body .verify-button {
    padding: 12px;
    background-color: #61dafb;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 18px;
    margin-top: 10px;
}

body.otp-page-body .verify-button:hover {
    background-color: #21a1f1;
}

body.otp-page-body .resend {
    color: #61dafb;
    cursor: pointer;
    text-decoration: underline;
}

body.otp-page-body .error {
    color: red;
    margin-top: 10px;
}

body.otp-page-body .message {
    color: green;
    margin-top: 10px;
}


/* Media queries for smaller screens */

/* For laptops */
@media (max-width: 1024px) {
    body.otp-page-body .header-container {
      padding: 10px 0;
      height: 60px;
    }
  
    body.otp-page-body .logo {
      height: 60px;
      margin-top: 20px;
      margin-bottom: 20px;
    }
  
    body.otp-page-body .header-container h1 {
      font-size: 24px;
    }
  
    body.otp-page-body .otp-content {
      padding: 15px;
    }
  
    body.otp-page-body .otp-inputs input {
      width: 40px;
      height: 40px;
      font-size: 20px;
    }
  
    body.otp-page-body .verify-button {
      padding: 10px;
      font-size: 16px;
    }
  }
  
  /* For iPads and tablets */
  @media (max-width: 768px) {
    body.otp-page-body .header-container {
      padding: 10px 0;
      height: 50px;
    }
  
    body.otp-page-body .logo {
      height: 50px;
      margin-top: 15px;
      margin-bottom: 15px;
    }
  
    body.otp-page-body .header-container h1 {
      font-size: 22px;
    }
  
    body.otp-page-body .otp-content {
      padding: 10px;
    }
  
    body.otp-page-body .otp-image {
      width: 120px;
      height: 120px;
      margin-bottom: 15px;
    }
  
    body.otp-page-body .otp-inputs input {
      width: 35px;
      height: 35px;
      font-size: 18px;
      margin: 0 3px;
    }
  
    body.otp-page-body .verify-button {
      padding: 8px;
      font-size: 14px;
    }
  }
  
  /* For iPhones and Samsung phones */
  @media (max-width: 480px) {
    body.otp-page-body .header-container {
      padding: 5px 0;
      height: 40px;
    }
  
    body.otp-page-body .logo {
      height: 40px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  
    body.otp-page-body .header-container h1 {
      font-size: 18px;
    }
  
    body.otp-page-body .otp-content {
      padding: 5px;
    }
  
    body.otp-page-body .otp-image {
      width: 100px;
      height: 100px;
      margin-bottom: 10px;
    }
  
    body.otp-page-body .otp-inputs input {
      width: 30px;
      height: 30px;
      font-size: 16px;
      margin: 0 2px;
    }
  
    body.otp-page-body .verify-button {
      padding: 6px;
      font-size: 12px;
    }
  }
