.dashboard-header-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    background-color: #282c34;
    color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    box-sizing: border-box;
    position: relative;
    z-index: 10;
  }
  
  .dashboard-logo-container {
    display: flex;
    align-items: center;
    margin-left: 20px;
  }
  
  .dashboard-logo {
    height: 60px;
    margin-right: 15px;
  }
  
  .dashboard-header-options {
    display: flex;
    align-items: center;
    gap: 65px; /* Adjusted space between icons */
  }
  
  .dashboard-icon {
    cursor: pointer;
    color: white;
    font-size: 30px; /* Increased icon size */
  }
  
  .dashboard-notification-dropdown,
  .dashboard-profile-dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dashboard-dropdown-content {
    display: none;
    position: absolute;
    right: 0;
    background-color: white;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 20;
    color: black;
  }
  
  .dashboard-notification-dropdown:hover .dashboard-dropdown-content,
  .dashboard-profile-dropdown:hover .dashboard-dropdown-content {
    display: block;
  }
  
  .dashboard-dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
  }
  
  .dashboard-dropdown-content a:hover {
    background-color: #ddd;
  }
  
  .dashboard-dropdown-content button {
    width: 100%;
    padding: 12px 16px;
    background: none;
    border: none;
    text-align: left;
    cursor: pointer;
    font-size: 16px;
    color: black;
  }
  
  .dashboard-dropdown-content button:hover {
    background-color: #ddd;
  }
  
  .dashboard-report-button button {
    padding: 15px 30px; /* Increased button padding */
    background-color: #61dafb;
    border: none;
    border-radius: 5px;
    color: white;
    cursor: pointer;
    font-size: 18px; /* Increased button size */
  }
  
  .dashboard-report-button button:hover {
    background-color: #21a1f1;
  }
  
  /* New notifications style */
  .new-notification {
    color: red;
  }
  
  /* IntelliMonitor heading style */
  .intelli-monitor-heading {
    margin-left: 10px; /* Adjust the value to position it closer to the logo */
    font-size: 24px;
    font-weight: bold;
    color: white;
    z-index: 11;
  }
  
  /* Media queries for smaller screens */
  
  /* For laptops */
  @media (max-width: 1024px) {
    .dashboard-header-container {
      padding: 10px 15px; /* Adjust padding for smaller screens */
    }
  
    .dashboard-logo {
      height: 50px; /* Adjust logo size */
      margin-right: 10px; /* Adjust margin */
    }
  
    .dashboard-header-options {
      gap: 50px; /* Adjust gap between icons */
    }
  
    .dashboard-icon {
      font-size: 25px; /* Adjust icon size */
    }
  
    .dashboard-report-button button {
      padding: 12px 25px; /* Adjust button padding */
      font-size: 16px; /* Adjust button font size */
    }
  }
  
  /* For iPads and tablets */
  @media (max-width: 768px) {
    .dashboard-header-container {
      padding: 10px; /* Adjust padding for tablets */
      flex-direction: column; /* Stack elements vertically */
    }
  
    .dashboard-logo-container {
      margin-left: 0; /* Remove left margin */
      margin-bottom: 10px; /* Add bottom margin */
    }
  
    .dashboard-header-options {
      gap: 30px; /* Adjust gap between icons */
      justify-content: center; /* Center align icons */
    }
  
    .dashboard-icon {
      font-size: 22px; /* Adjust icon size */
    }
  
    .dashboard-report-button button {
      padding: 10px 20px; /* Adjust button padding */
      font-size: 14px; /* Adjust button font size */
    }
  }
  
  /* For iPhones and Samsung phones */
  @media (max-width: 480px) {
    .dashboard-header-container {
      padding: 5px; /* Adjust padding for phones */
      flex-direction: column; /* Stack elements vertically */
    }
  
    .dashboard-logo {
      height: 40px; /* Adjust logo size */
      margin-right: 5px; /* Adjust margin */
    }
  
    .dashboard-header-options {
      gap: 20px; /* Adjust gap between icons */
      justify-content: center; /* Center align icons */
    }
  
    .dashboard-icon {
      font-size: 20px; /* Adjust icon size */
    }
  
    .dashboard-report-button button {
      padding: 8px 15px; /* Adjust button padding */
      font-size: 12px; /* Adjust button font size */
    }
  }